


@media only screen and (min-width: 320px) and (max-width: 992px) {

  #whoIAmSection > div:nth-child(1) {
    display: none;
    }
  
  
    .picSection {
    margin-top: 50%;
    }
  
  
}


#projects > div:nth-child(1) > h2:nth-child(1) {
  color: black;
 }
 
.whoIAm {
  padding-top: 80px;


  .presentationContainer {
    padding: 20px 0px;
    display: flex;
    @include tabAndLess{
      flex-direction: column;
    }
    .picSection {
      .profilePic {
        width: 330px;
        padding: 0px 40px;
        border-radius: 28%;
        @include smartphoneOnly{
          width: 200px;
        }
      }
    }
    .presentation {
      padding: 0px 20px;
      .presentationP {
        text-align: start;
        font-weight: 500;
        @include tabAndLess{
          text-align: center;
        }
        @include tabOnly{
          font-size: 20px;
        }
        @include smartphoneOnly{
          font-size: 14px;
        }
      }
      
    }
  }
  .mediaIcons {
    display: flex;
    margin: 50px 120px;
    .icons {
      margin-left: 20px;
      color: $millbrook;
      font-size: 26px;
      cursor: pointer;
      transition-duration: 150ms;
      &:hover {
        scale: 1.3;
      }
    }
  }
}

.picSection > .downloadCVButton {
  display: flex;
  justify-content: center;

  .downloadCVLink {
    margin: 14%;
    text-decoration: none;
    font-size: 16px;
    background: $millbrook;
    color: #fff;
    padding: 15px 40px;
    border-style: none;
    border-radius: 15px;
    font-family: "Raleway";
    transition-duration: 300ms;
    cursor: pointer;
    @include tabAndLess{
      margin: 50px;
    }
    &:hover {
      font-weight: 700;
      scale: 1.1;
    }
   
  }
}

