.collapse-header {
  position: relative;
  height: 47px;
  background: linear-gradient(90deg, rgb(131, 58, 180) 0%, rgb(253, 29, 29) 50%, rgb(252, 176, 69) 100%);
  display: flex;
  align-items: center;
  justify-content: left;
  font-weight: bold;
  cursor: pointer;
  padding-left: 15px;
  color: #ffffff;
}
.collapse-header .chevron {
  position: absolute;
  top: 50%;
  right: 15px;
  border-style: solid;
  border-width: 0.25em 0.25em 0 0;
  content: "";
  display: inline-block;
  height: 0.45em;
  width: 0.45em;
  transition: transform 0.2s ease;
}
.collapse-header .chevron.open:before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.collapse-header.open .chevron {
  transform: translateY(-50%) rotate(135deg);
}

.collapse-content {
  background: #f6f6f6;
  overflow: hidden;
  transition: height 0.3s ease;
}
.collapse-content.collapsed {
  height: 0;
}
.collapse-content.expanded {
  height: auto;
  text-align: center;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 97%;
  line-height: 142.6%;
  color: #ff6060;
}
.collapse-content.expanded p {
  text-align: justify;
  margin-left: 3%;
  margin-right: 3%;
  color: black;
  font-size: 17px;
  font-family: raleway;
}

