.music-player {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
  width: 63%;
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    
    li {
      margin: 5px 0;
      
      button {

        border: none;
        font-size: 16px;
        cursor: pointer;
        transition: all 0.2s ease;
        
        &:hover {
          color: #3e3e3e;
        }
      }
    }
  }

  
  
  .current-song {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
    text-align: center;
    
    h2 {
      margin: 0;
      font-size: 28px;
      font-weight: bold;
    }
    
    p {
      margin: 5px 0;
      font-size: 16px;
      color: #757575;
    }
    
    audio {
      margin-top: 20px;
      width: 100%;
      max-width: 500px;
    }
  }
  
  .controls {
    height: 185px;

    button {
      background: none;
      border: none;
      font-size: 24px;
      color: #757575;
      cursor: pointer;
      margin: 0 10px;
      transition: all 0.2s ease;
      outline: none;
      
      &:hover {
        color: #3e3e3e;
      }
      
      &:active {
        transform: translateY(2px);
      }
    }
    .music-player .controls input[type=range] {
      width: 80%;
      margin-left: 10px;
    }
    .progress-slider {
      width: 68%;
      margin: 0 20px;
      
      &::-webkit-slider-thumb {
        appearance: none;
        width: 12px;
        height: 12px;
        background-color: #3e3e3e;
        border-radius: 50%;
        cursor: pointer;
      }
      
      &::-moz-range-thumb {
        width: 12px;
        height: 12px;
        background-color: #3e3e3e;
        border-radius: 50%;
        cursor: pointer;
      }
      
      &::-ms-thumb {
        width: 12px;
        height: 12px;
        background-color: #3e3e3e;
        border-radius: 50%;
        cursor: pointer;
      }
      
      &::-webkit-slider-runnable-track {
        height: 4px;
        background-color: #d8d8d8;
        border-radius: 4px;
      }
      
      &::-moz-range-track {
        height: 4px;
        background-color: #d8d8d8;
        border-radius: 4px;
      }
      
      &::-ms-track {
        height: 4px;
        background-color: #d8d8d8;
        border-radius: 4px;
      }
    }
    .volume {
      display: flex;
      align-items: center;
      margin-left: 20px;
      position: relative;
      
      .volume-slider-container {
        background-color: #fff;
        border: 1px solid #d8d8d8;
        border-radius: 4px;
        padding: 6px;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
        z-index: 1;
        
        .volume-slider {
          width: 100%;
          
          &::-webkit-slider-thumb {
            appearance: none;
            width: 12px;
            height: 12px;
            background-color: #3e3e3e;
            border-radius: 50%;
            cursor: pointer;
          }
          
          &::-moz-range-thumb {
            width: 12px;
            height: 12px;
            background-color: #3e3e3e;
            border-radius: 50%;
            cursor: pointer;
          }
          
          &::-ms-thumb {
            width: 12px;
            height: 12px;
            background-color: #3e3e3e;
            border-radius: 50%;
            cursor: pointer;
          }
          
          &::-webkit-slider-runnable-track {
            height: 4px;
            background-color: #d8d8d8;
            border-radius: 4px;
          }
          
          &::-moz-range-track {
            height: 4px;
            background-color: #d8d8d8;
            border-radius: 4px;
          }
          
          &::-ms-track {
            height: 4px;
            background-color: #d8d8d8;
            border-radius: 4px;
          }
        }
      }
      
      .volume-slider {
        width: 100%;
        height: 8px;
        appearance: none;
        margin: 0;
        background-color: #d8d8d8;
        border-radius: 4px;
        outline: none;
        
        &::-webkit-slider-thumb {
          appearance: none;
          width: 16px;
          height: 16px;
          background-color: #3e3e3e;
          border-radius: 50%;
          cursor: pointer;
          margin-top: -4px;
        }
        
        &::-moz-range-thumb {
          width: 16px;
          height: 16px;
          background-color: #3e3e3e;
          border-radius: 50%;
          cursor: pointer;
          margin-top: -4px;
        }
        
        &::-ms-thumb {
          width: 16px;
          height: 16px;
          background-color: #3e3e3e;
          border-radius: 50%;
          cursor: pointer;
          margin-top: -4px;
        }
        
        &::-webkit-slider-runnable-track {
          height: 4px;
          background-color: #d8d8d8;
          border-radius: 4px;
        }
        
        &::-moz-range-track {
          height: 4px;
          background-color: #d8d8d8;
          border-radius: 4px;
        }
        
        &::-ms-track {
          height: 4px;
          background-color: #d8d8d8;
          border-radius: 4px;
        }
      }
    }
  }
}

        
